body {
    width: 100%;
    height: 100vh;
    background-color: #1f1f1f;
}

.link {
    position: relative;
    border-radius: 6px;
}

.btn-copy {
    display: inline-block;
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    transition: 0.3s;
    width: auto;
    z-index: 10;
}

.btn-copy:hover {
    color: #9a9a9a;
}

.btn-copy-info .btn-copy-info-text {
    visibility: hidden;
    width: 140px;
    background-color: #555;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px;
    position: relative;
    left: -10px;
    z-index: 1;
    opacity: 0;
    transition: opacity 0.3s;
}

.btn-copy-info .btn-copy-info-text::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -5px;
    margin-right: -10px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent transparent #555;
}

.btn-copy-info:hover .btn-copy-info-text {
    visibility: visible;
    opacity: 1;
}

.file-type {
    width: 250px;
    height: 275px;
    overflow: hidden;
}